* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  padding: 0;
}
.list-item {
  display: flex;
  list-style: none;
  margin-top: 10px;
  margin-bottom: 10px;
}
.box-flag {
  display: flex;
  width: 60px;
  height: 40px;
}
.img-flag {
  display: flex;

  width: 100%;
  height: 100%;
}
.country-name {
  margin: auto 0 auto 10px;
  font-weight: 700;
  font-size: 30px;
}
.text-element {
  font-weight: 700;
  font-size: 20px;
  color: rgb(20, 3, 31);
}
